import React, { useEffect, useState } from 'react'
import NewsItem from './NewsItem'
import Spinner from './Spinner';
import PropTypes from 'prop-types'
import InfiniteScroll from "react-infinite-scroll-component";

const News = (props) => {
  const [articles, setArticles] = useState([])
  const [loading, setLoading] = useState(true)
  const [page, setPage] = useState(1)
  const [totalResults, setTotalResults] = useState(0)
  const [error, setError] = useState(null)

  const capitalizeFirstLetter = (string) => {
    return string.charAt(0).toUpperCase() + string.slice(1);
  }

  // NEW: use the "everything" Apple query you provided, with page & pageSize
  const buildEverythingUrl = (pageNumber = 1) => {
    // note: keep apiKey dynamic via props.apiKey
    return `https://newsapi.org/v2/everything?q=apple&from=2025-11-30&to=2025-11-30&sortBy=popularity&apiKey=${props.apiKey}&page=${pageNumber}&pageSize=${props.pageSize}`;
  };

  const updateNews = async () => {
    try {
      setError(null);
      props.setProgress && props.setProgress(10);
      setLoading(true);

      const url = buildEverythingUrl(1);
      console.log("DEBUG updateNews URL:", url);

      const res = await fetch(url);
      props.setProgress && props.setProgress(30);

      const parsedData = await res.json();
      console.log("DEBUG updateNews parsedData:", parsedData);

      props.setProgress && props.setProgress(70);

      // handle API error response
      if (parsedData && parsedData.status && parsedData.status !== 'ok') {
        setError(parsedData.message || 'API returned an error');
        setArticles([]);
        setTotalResults(0);
        setPage(1);
        setLoading(false);
        props.setProgress && props.setProgress(100);
        return;
      }

      const newArticles = parsedData && Array.isArray(parsedData.articles) ? parsedData.articles : [];
      const newTotal = parsedData && typeof parsedData.totalResults === 'number' ? parsedData.totalResults : 0;

      setArticles(newArticles);
      setTotalResults(newTotal);
      setPage(1);
      setLoading(false);
      props.setProgress && props.setProgress(100);
    } catch (err) {
      console.error("updateNews failed:", err);
      setError("Network or parsing error. Check console for details.");
      setArticles([]);
      setTotalResults(0);
      setPage(1);
      setLoading(false);
      props.setProgress && props.setProgress(100);
    }
  }

  useEffect(() => {
    document.title = `${capitalizeFirstLetter(props.category)} - NewsMonkey`;
    updateNews();
    // eslint-disable-next-line
  }, [props.category, props.pageSize, props.country]);

  const fetchMoreData = async () => {
    try {
      setPage(prevPage => {
        const nextPage = prevPage + 1;

        (async () => {
          try {
            const url = buildEverythingUrl(nextPage);
            console.log("DEBUG fetchMoreData URL:", url);

            const res = await fetch(url);
            const parsedData = await res.json();
            console.log("DEBUG fetchMoreData parsedData:", parsedData);

            if (parsedData && parsedData.status && parsedData.status !== 'ok') {
              setError(parsedData.message || 'API error while fetching more');
              return;
            }

            const moreArticles = parsedData && Array.isArray(parsedData.articles) ? parsedData.articles : [];
            const newTotal = parsedData && typeof parsedData.totalResults === 'number' ? parsedData.totalResults : totalResults;

            setArticles(prev => [...prev, ...moreArticles]);
            setTotalResults(newTotal);
          } catch (innerErr) {
            console.error("fetchMoreData inner error:", innerErr);
            setError("Network error while loading more articles. Check console.");
          }
        })();

        return nextPage;
      });
    } catch (err) {
      console.error("fetchMoreData outer error:", err);
      setError("Unexpected error while loading more articles.");
    }
  };

  return (
    <>
      <h1 className="text-center" style={{ margin: '35px 0px', marginTop: '90px' }}>
        NewsMonkey - Top {capitalizeFirstLetter(props.category)} Headlines
      </h1>

      {loading && <Spinner />}

      {/* Show API / network errors */}
      {!loading && error && (
        <div className="container">
          <div className="alert alert-danger text-center" role="alert">
            {error}
          </div>
        </div>
      )}

      {/* No results friendly message */}
      {!loading && !error && (!Array.isArray(articles) || articles.length === 0) && (
        <div className="container">
          <p className="text-center">No articles found.</p>
        </div>
      )}

      <InfiniteScroll
        dataLength={Array.isArray(articles) ? articles.length : 0}
        next={fetchMoreData}
        hasMore={Array.isArray(articles) ? articles.length < totalResults : false}
        loader={<Spinner />}
        style={{ overflow: 'visible' }}
      >
        <div className="container">
          <div className="row">
            {Array.isArray(articles) && articles.map((element, index) => {
              return (
                <div className="col-md-4" key={element.url ? element.url : index}>
                  <NewsItem
                    title={element.title || ""}
                    description={element.description || ""}
                    imageUrl={element.urlToImage}
                    newsUrl={element.url}
                    author={element.author}
                    date={element.publishedAt}
                    source={(element.source && element.source.name) ? element.source.name : "Unknown"}
                  />
                </div>
              )
            })}
          </div>
        </div>
      </InfiniteScroll>
    </>
  )
}

News.defaultProps = {
  country: 'in',
  pageSize: 8,
  category: 'general',
}

News.propTypes = {
  country: PropTypes.string,
  pageSize: PropTypes.number,
  category: PropTypes.string,
  apiKey: PropTypes.string, // pass your API key via props.apiKey
  setProgress: PropTypes.func
}

export default News
