// src/components/NewsItem.js
import React from "react";

const placeholder =
  "https://via.placeholder.com/600x400?text=No+Image";

const NewsItem = ({ title, description, imageUrl, newsUrl, author, date, source }) => {
  const shortTitle = title && title.length > 65 ? title.slice(0, 62) + "..." : title;
  const shortDesc = description && description.length > 110 ? description.slice(0, 107) + "..." : description;

  return (
    <div className="news-card">
      <div className="card h-100 border-0 shadow-sm overflow-hidden">
        <div className="card-img-wrapper" style={{ position: "relative" }}>
          <img
            src={imageUrl || placeholder}
            alt={title}
            className="card-img-top news-img"
            onError={(e) => { e.target.onerror = null; e.target.src = placeholder; }}
          />
          <span className="badge source-badge">{source || "Unknown"}</span>
        </div>

        <div className="card-body d-flex flex-column">
          <h5 className="card-title" title={title}>{shortTitle}</h5>
          <p className="card-text text-muted" title={description}>{shortDesc}</p>

          <div className="mt-auto d-flex justify-content-between align-items-center">
            <small className="text-muted">By {author || "Unknown"}</small>
            <small className="text-muted">{date ? new Date(date).toLocaleString() : ""}</small>
          </div>

          <div className="d-flex mt-3">
            <a
              href={newsUrl}
              target="_blank"
              rel="noreferrer"
              className="btn btn-gradient btn-sm me- auto"
            >
              Read More
            </a>
            <a
              href={newsUrl}
              target="_blank"
              rel="noreferrer"
              className="btn btn-outline-secondary btn-sm"
              style={{ marginLeft: 8 }}
            >
              Share
            </a>
          </div>
        </div>
      </div>
    </div>
  );
};

export default NewsItem;
